var url = window.location.pathname;
var urljude = url.split("/");
console.log(urljude[2]);

switch (urljude[2]) {
  case "clothingBags.html":
    clothingtabs();
    break;

  case "breathe.html":
    breathe();
    break;

  case "phonesmartisan.html":
    smartisanphone();
    break;

  case "parts.html":
    parts();
    break;

  case "dyCulturalandCreative.html":
    dycuilural();
    break;

  case "sweepingmachine.html":
    sweepingmachine();
    break;

  case "TNTdisplay.html":
    tntdisplay();
    break;

  case "goodselect.html":
    goodselect();
    break;
}